import React, { useState, useEffect } from 'react';
import LoaderPage from './LoaderPage';

export default function Result({ data_Recived }) {
  const dataSend = JSON.stringify(data_Recived, null, 2);

  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Fetch the prediction result from the backend
    fetch('/predict', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: dataSend,
    })
      .then(response => response.json())
      .then(result => {
        setData(result.predicted_price);
        setLoading(false);
      })
      .catch(error => {
        console.error(error);
        setLoading(false);
      });
  }, [dataSend]);

  return (
    <div className="content-card home">
      {loading ? (
        <LoaderPage />
      ) : (
        <div style={styles.resultContainer}>
          <h1 style={styles.resultHeader}>The Predicted Price of the given car is</h1>
          <h2 style={styles.predictedPrice}>{data}</h2>
          <div style={styles.carDataContainer}>
  <h3 style={styles.carDataHeader}>Car Data:</h3>
  <div style={styles.carData}>{formatCarData(data_Recived)}</div>
</div>

        </div>
      )}
    </div>
  );
}

const formatCarData = carData => {
  return Object.entries(carData).map(([key, value]) => (
    <div key={key} style={styles.carDataEntry}>
      <span style={styles.carDataKey}>{key}:</span> {value}
    </div>
  ));
};



const styles = {
  resultContainer: {
    textAlign: 'center',
  },
  carDataEntry:{
    fontWeight:'500'
  },
  // carDataKey:{
  //   fontWeight:'200'
  // },
  resultHeader: {
    fontSize: '24px',
    fontWeight: 'bold',
    marginBottom: '20px',
  },
  predictedPrice: {
    fontSize: '32px',
    fontWeight: 'bold',
    color: 'green',
    marginBottom: '30px',
  },
  carDataContainer: {
    border: '1px solid green',
    borderRadius: '5px',
    padding: '10px',
    marginTop: '20px',
  },
  carDataHeader: {
    fontSize: '18px',
    marginBottom: '10px',
  },
  carData: {
    fontSize: '14px',
    whiteSpace: 'pre-wrap',
  },
};
