import React, { useState } from 'react';
import { Form } from 'react-bootstrap';
import './LP.css';
import LoaderBar from './LoaderBar';
import CarLoader from './CarLoader';

export default function PkWheelsScraper() {
  const [data, setData] = useState();
  const [warning, setWarning] = useState('Note: the record you input also affects loading time');
  const [loading, setLoading] = useState(false);
  const [percent, setPercent] = useState(0);
  const [fetchedData, setFetchedData] = useState(null);
  const [showDownloadButton, setShowDownloadButton] = useState(false);

  const Process = async (e) => {
    e.preventDefault();
    setLoading(true);
    setWarning('Please wait. We are getting your files.');
  
    const numRecords = parseInt(data);
    setPercent(0);
  
    try {
      const response = await fetch('/fetch_data', {
        method: 'POST',                   // Sending a POST request
        headers: {
          'Content-Type': 'application/json',  // Specifying JSON content type
        },
        body: JSON.stringify({ numRecords }), // Sending the data as JSON
      });
  
      if (response.ok) {
        const data = await response.json();
  
        // Display processing time for each record
        const dataWithProcessingTime = data.map(item => ({
          ...item,
          processingTime: item.processingTime.toFixed(2),
        }));
  
        setFetchedData(dataWithProcessingTime);
        setShowDownloadButton(true);
      } else {
        console.error('Error fetching data');
      }
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
      setWarning(`Processing complete for ${numRecords} records.`);
    }
  };
  

  const handleDownload = () => {
    if (fetchedData) {
      const blob = new Blob([JSON.stringify(fetchedData)], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'calculated_data.json';
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    }
  };

  return (
    <div className="content-card home" id="welcome-section">
      <h2>Enter the number of records to be tested</h2>
      <br />
      <br />
      <Form controlId="Mileage" style={{ width: '100px' }}>
        <Form.Control
          type="number"
          onChange={(e) => setData(e.target.value)}
          required
        />
      </Form>
      <br />
      <br />
      <button className="btn btn-success" onClick={Process}>
        Submit
      </button>
      {showDownloadButton && (
        <button className="btn btn-primary" onClick={handleDownload}>
          Download Calculated Data
        </button>
      )}
      <br />
      <p>{warning}</p>
      {loading && <CarLoader percent={percent.toString()} />}
      {loading && <LoaderBar percent={percent.toString()} />}
    </div>
  );
}
